<template>
	<div class="page">
		
		<head-back></head-back>
		
		 <div class="content">
			<form action="" method="" id="" autocomplete="off">
				<div class="list-block content-martop10 content-marbottom10 content-boxshow">
					<ul>
						<li>
							<div class="item-content  border-bottom content-padrights" >
								<div class="marginauto color-333">
									<em class="colorffb"></em> &gt; <em class="colorffb"></em>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<div class="list-block list-margin content-marbottom10 content-martop10 content-boxshow">
					<ul>
						<li>
							<div class="item-content   content-padrights" >
								<div class="item-inner border-bottom">
									<div class="item-title label color-333"></div>
									<div class="item-input">
										<input type="text" id="username" v-model="username" placeholder="">
									</div>
								</div>
							</div>
						</li>
						<li>
							<div class="item-content   content-padrights" >
								<div class="item-inner border-bottom">
									<div class="item-title label color-333"></div>
									<div class="item-input" >
										<input type="password" id="regPassword" v-model="regPassword" placeholder="">
									</div>
								</div>
							</div>
						</li>
						<li>
							<a  class="item-link  display-block width100"  >
							<div class="item-content  content-padrights" >
								<div class="item-inner border-bottom" >
									<div class="item-title label color-333"></div>
									<select id="obtain" v-model="obtain">
										<option value="0">5()</option>
										<option value="1"></option>
										<option value="2"></option>
										<option value="3"></option>
										<option value="4"></option>
										<option value="5"></option>
									</select>
								</div>
							</div>
							</a>
						</li>
						<li>
							<div class="item-content   content-padrights" >
								<div class="item-inner ">
									<div class="item-title label color-333"></div>
									<div class="item-input" >
										<input type="text" id="recommend" v-model="recommend" placeholder="40()">
									</div>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<div class="padding575">
					<a id="register_ii" class="button button-big button-light bgcolor-cfd colorfff"></a>
				</div>
			</form>
		</div>
	</div>
		
	<panel></panel>
		
</template>

<script>
	import headBack from "../components/headBack.vue"
	import panel from "../components/panel.vue"
	import store from "../vuex/store.js"
	import getters from "../vuex/getters.js"
	export default {
		name: "register2",
		data(){
			return {
				prevent: false,
				username: '',
				regPassword: '',
				obtain: '0',
				recommend: ''
			}
		},
		methods: {
			eventBind(){
				var _csrf = $('meta[name="csrf-token"]').attr("content");
				
				var name_exp = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
				var num_exp = /^[0-9]+$/;
				
				function checkName(){
					var content = $('#username').val();
					if(content.length>15 || content.length<5){
						$.toast('5-15');
						return false;
					}else if(!name_exp.test(content)){
						$.toast('');
						return false;
					}else if(num_exp.test(content)){
						$.toast('');
						return false;
					}else{
						//
						var ajaxReturn = true;
						/*$.ajax({
							url:'/verify/checkusername',
							type:'post',
							data:{"_csrf":_csrf,"username":content},
							async:false,
							dataType:'json',
							success:function(msg){
								if(msg.code==1){
									ajaxReturn = true;
								}else{
									$.toast('');
								}
							},
							cache:false
						});*/
						return ajaxReturn;
					}
				}
				
				function checkPassword(){
					var content = $('#regPassword').val();
					if(content.length>16||content.length<6){
						$.toast('6-16');
						return false;
					}else if(/\s/g.test(content)){
						$.toast('');
						return false;
					}else if(num_exp.test(content)){
						$.toast('');
						return false;
					}else{
						return true;
					}
				}
				
				function checkInviter(){
					var content = $('#recommend').val();
					if(content.length==0){
						return true;
					}else if(!name_exp.test(content)){
						$.toast('');
						return false;
					}else{
						//
						var ajaxReturn = true;
						/*$.ajax({
							url:'/verify/checkinviter',
							type:'post',
							data:{"_csrf":_csrf,"username":content},
							async:false,
							dataType:'json',
							success:function(msg){
								if(msg.code==1){
									ajaxReturn = true;
								}else{
									$.toast('');
								}
							},
							cache:false
						});*/
						return ajaxReturn;
					}
				}
				
				$('#username').blur(checkName);
				
				$('#regPassword').blur(checkPassword);
				
				$('#recommend').blur(checkInviter);
				
				$('#register_ii').click(function(){
					if(checkName()==true && checkPassword()==true && checkInviter()==true){
						/*$.ajax({
							url:'',
							type:'post',
							data:{"_csrf":_csrf,"username":username,"password":password,"obtain":obtain,"recommend":recommend},
							async:false,
							dataType:'json',
							success:function(msg){
								if(msg.code==1){
									location.href = "/#/register3";
								}else{
									$.toast(msg.message);
								}
							},
							cache:false
						});*/
						location.href = "/#/register3";
					}
				});
			}
		},
		components: {
			headBack,
			panel
		},
		store: store,
		vuex: {
			getters: {
				isLogin: getters.loginStatus
			}
		},
		route: {
			data(){
				console.log(this.$route.path + ' step now');
				if(this.prevent)location.href = "/#/login";
				this.username = '';
				this.regPassword = '';
				this.obtain = '0';
				this.recommend = '';
			}
		},
		ready(){
			this.eventBind()
			$.init()
		}
	}
</script>


