<template>
	<div class="panel-overlay"></div>
	<div class="panel panel-right panel-reveal theme-dark list-margin" id="panel-right">
		<div class="list-margin">
			<div class="right-log"></div>
		</div>
		<div class="list-block list-margin">
			<ul class="rightmenu" >
				<li><a v-on:click.prevent href="/#/"></a></li>
				<li><a v-on:click.prevent href="/#/list"></a></li>
				<li><a v-on:click.prevent href="/#/user" v-on:click="increment"></a></li>
				<li><a v-on:click.prevent href="javascript:;"></a></li>
				<li><div class="border-bottom"></div></li>
				<li v-show="!isLogin"><a v-on:click.prevent href="/#/register1"></a></li>
				<li v-show="!isLogin"><a v-on:click.prevent href="/#/login"></a></li>
				<li v-show="isLogin"><a v-on:click.prevent href="javascript:;"></a></li>
				<li v-show="isLogin"><a v-on:click.prevent class="out" href="/#/login"></a></li>
			</ul>
		</div>
	</div>
</template>

<script>
	import getters from "../vuex/getters.js"
	import actions from "../vuex/actions.js"
	export default {
		name: "panel",
		el: "panel",
		data(){
			return {
			
			}
		},
		methods: {
			eBind(){
				var _self = this;
				$('#panel-right').on('opened',function(){
					$(this).off('closed');
				});
				$('.rightmenu a').click(function(){
					if(!$(this).is('.out')){
						var url = $(this).prop('href');
						$('#panel-right').on('closed',function(){
							location.href = url;
						});
						$.closePanel();
					}else{
						var cookies = document.cookie.split(';');
						var token = '';
						for(var i=0; i<cookies.length; i++){
							if(cookies[i].split('=')[0]=='token')break;
						}
						if(i < cookies.length)token = cookies[i].split('=')[1];
						$.ajax({
							url: '/static/data/tokenState.json',
							data: {"token": token},
							dataType: 'json',
							async: false,
							success: function(msg){
								if(msg.code==1){
									$.toast('');
									_self.loginOut();
									$('#panel-right').on('closed',function(){
										location.href = "/#/login";
									});
									$.closePanel();
								}else{
									$.toast(msg.message);
								}
							},
							cache:false
						});
					}
				});
			}
		},
		components: {
		
		},
		vuex: {
			getters: {
				isLogin: getters.loginStatus
			},
			actions: {
				loginOut: actions.loginOut
			}
		},
		ready(){
			this.eBind()
		}
	}
</script>


